@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: rgb(189, 7, 7);
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 1rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}
table.propiedades .lista td {
  box-sizing: border-box;
  border-bottom: 1.5px solid #979393;
  padding-bottom: 2rem;
}

.imagen-small {
  width: 20rem;
}